@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body,button{font-family:"Poppins", sans-serif}.song-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-height:55vh}.song-container img{margin:1rem;height:500px;-webkit-clip-path:circle(250px at center);clip-path:circle(250px at center)}@media screen and (max-width: 768px){.song-container img{height:250px;-webkit-clip-path:circle(125px at center);clip-path:circle(125px at center);margin:5rem}}.player{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;width:40%;min-height:15vh;margin:auto}.player h4{width:50px}.player input{width:100%;-webkit-appearance:none;background-color:transparent;cursor:pointer;outline:none}.player input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;width:16px;height:16px;background-color:transparent}.player-container{width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.player-icons{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;padding:1rem 3rem}.player-icon{cursor:pointer;width:5rem;height:2.5rem}.track{background:linear-gradient(90deg, #b4983f 0%, #f1dc6e 48%, #b89e44 100%);-webkit-flex:1 1;flex:1 1;margin:0rem 2rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;position:relative;border-radius:1rem;overflow:hidden}.animate-track{background-color:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;-webkit-transform:translateX(10%);transform:translateX(10%);pointer-events:none}@media screen and (max-width: 768px){.player{width:80%;display:-webkit-flex;display:flex;margin:5rem 3rem}}.library{position:fixed;left:0;top:0;width:25rem;height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;overflow:scroll;transition:all 0.5s ease-in-out;z-index:1;background:white;-webkit-transform:translateX(-100%);transform:translateX(-100%);opacity:0;box-shadow:5px 5px 50px rgba(0,0,0,0.2);scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5)}.library h1{text-align:center;font-style:italic;width:100%;box-shadow:0px -3px 30px rgba(0,0,0,0.2)}.library::-webkit-scrollbar{width:10px}.library::-webkit-scrollbar-track{background:transparent}.library::-webkit-scrollbar-thumb{background-color:#9fb3f0;border-radius:20px;border:transparent}.library-songs{-webkit-flex:1 1;flex:1 1}.library-song{display:-webkit-flex;display:flex;padding:2rem 0rem;-webkit-align-items:center;align-items:center;transition:background-color 0.25s ease-in-out}.library-song img{width:10rem;-webkit-clip-path:circle(40px at center);clip-path:circle(40px at center)}.library-song:hover{background-color:rgba(236,84,84,0.37);cursor:pointer}.library-song-info h3{font-size:15px}.library-song-info h4{font-weight:normal}.selected{background-color:#c6d5f1}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:100%}@media screen and (max-width: 768px){.library{width:100%}.library h1{padding:1rem}}.nav{position:fixed;right:5%;top:3%}.nav button{transition:background-color 0.25s ease-in-out;background-color:transparent;border:3px solid #4d4949;padding:0.5rem;width:100px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;cursor:pointer;color:#4d4949}.nav button:hover{background-color:#4d4949;color:white}@media screen and (max-width: 768px){.nav{right:0;top:0;z-index:2}.nav button{position:absolute;margin:1rem;right:0}}

