.song-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-height: 55vh;

  img {
    margin: 1rem;
    height: 500px;
    clip-path: circle(250px at center);
  }
}

@media screen and (max-width: 768px) {
  .song-container {
    img {
      height: 250px;
      clip-path: circle(125px at center);
      margin: 5rem;
    }
  }
}
