.nav {
  position: fixed;
  right: 5%;
  top: 3%;

  button {
    transition: background-color 0.25s ease-in-out;
    background-color: transparent;
    border: 3px solid #4d4949;
    padding: 0.5rem;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;

    color: #4d4949;

    &:hover {
      background-color: #4d4949;
      color: white;
    }
  }
}

@media screen and (max-width: 768px) {
  .nav {
    right: 0;
    top: 0;

    z-index: 2;
    button {
      position: absolute;
      margin: 1rem;
      right: 0;
    }
  }
}
