.player {
  //   background-color: lightcoral;
  display: flex;
  flex-direction: column;

  justify-content: space-around;

  width: 40%;
  min-height: 15vh;
  margin: auto;

  h4 {
    width: 50px;
  }

  input {
    width: 100%;
    -webkit-appearance: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
  }

  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
    background-color: transparent;
  }
}

.player-container {
  width: 100%;
  // margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.player-icons {
  display: flex;
  justify-content: space-around;
  padding: 1rem 3rem;
}

.player-icon {
  cursor: pointer;
  // background-color: rebeccapurple;
  width: 5rem;
  height: 2.5rem;
}

.track {
  background: linear-gradient(
    90deg,
    rgba(180, 152, 63, 1) 0%,
    rgba(241, 220, 110, 1) 48%,
    rgba(184, 158, 68, 1) 100%
  );
  flex: 1;
  margin: 0rem 2rem;
  display: flex;
  align-items: center;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}

.animate-track {
  background-color: rgb(204, 204, 204);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(10%);
  pointer-events: none;
}

@media screen and (max-width: 768px) {
  .player {
    width: 80%;
    display: flex;
    margin: 5rem 3rem;
  }
}
