.library {
  position: fixed;
  left: 0;
  top: 0;
  width: 25rem;
  height: 100%;

  display: flex;
  flex-direction: column;
  overflow: scroll;

  transition: all 0.5s ease-in-out;

  z-index: 1;
  background: white;

  transform: translateX(-100%);
  opacity: 0;

  h1 {
    text-align: center;
    font-style: italic;
    width: 100%;
    box-shadow: 0px -3px 30px rgba($color: #000000, $alpha: 0.2);
  }

  box-shadow: 5px 5px 50px rgba($color: #000000, $alpha: 0.2);

  scrollbar-width: thin;
  scrollbar-color: rgba(155, 155, 155, 0.5);
}

.library::-webkit-scrollbar {
  width: 10px;
}
.library::-webkit-scrollbar-track {
  background: transparent;
}
.library::-webkit-scrollbar-thumb {
  background-color: rgb(159, 179, 240);
  border-radius: 20px;
  border: transparent;
}

.library-songs {
  flex: 1;
}

.library-song {
  display: flex;
  padding: 2rem 0rem;
  align-items: center;

  img {
    width: 10rem;
    clip-path: circle(40px at center);
  }
  transition: background-color 0.25s ease-in-out;
}

.library-song:hover {
  background-color: rgba(236, 84, 84, 0.37);
  cursor: pointer;
}

.library-song-info {
  h3 {
    font-size: 15px;
  }
  h4 {
    font-weight: normal;
  }
}

.selected {
  background-color: #c6d5f1;
}

.active-library {
  transform: translateX(0%);
  opacity: 100%;
}

@media screen and (max-width: 768px) {
  .library {
    h1 {
      padding: 1rem;
    }
    width: 100%;
  }
}
